import { useNavigate } from "react-router-dom";
import { HiHome } from "react-icons/hi";
import { Breadcrumb as CustomBreadcrumb } from "flowbite-react";

const Breadcrumb = () => {
  const navigate = useNavigate();

  return (
    <CustomBreadcrumb className="bg-transparent py-3 mb-2">
      <CustomBreadcrumb.Item
        href="#"
        onClick={() => navigate("/")}
        icon={HiHome}
      >
        Home
      </CustomBreadcrumb.Item>
      <CustomBreadcrumb.Item>{document.title}</CustomBreadcrumb.Item>
    </CustomBreadcrumb>
  );
};

export default Breadcrumb;
